import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
export default {
	data() {
		return {
			flag: false,
			hidden: true,
			title: '',
			desc: '',
			messages: '',
			id: '',
			add_time: ''
		};
	},
	components: {
		uniNavBar
	},
	onLoad() {
		var _this = this;
		uni.request({
			url: this.url + 'advicefeedback/',
			method: 'GET',
			data: {
				title: this.title,
				message: this.desc
			},
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success(res) {
				console.log(res);
				if (res.data.data == '') {
					_this.flag = true;
				} else {
					_this.flag = false;
				}
				_this.messages = res.data.data;
				for (let i = 0; i < _this.messages.length; i++) {
					var t = _this.messages[i].add_time;
					var t1 = t.substr(0, 10) + '  ' + t.substr(11, 8);
					_this.messages[i].add_time = t1;
					var up = _this.messages[i].updated_time;
					var up1 = up.substr(0, 10) + '  ' + up.substr(11, 8);
					_this.messages[i].updated_time = up1;
				}
			}
		});
	},
	onBackPress(option) {
		plus.key.hideSoftKeybord();
	},
	methods: {
		getTitleContent: function(e) {
			this.title = e.detail.value;
		},
		getDescContent: function(e) {
			this.desc = e.detail.value;
		},
		addMessage: function() {
			this.hidden = false;
		},
		close: function() {
			this.hidden = true;
		},
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
		submit: function() {
			var _this = this;
			if (this.title == '') {
				uni.showToast({
					title: '标题不能为空',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			if (this.desc == '') {
				uni.showToast({
					title: '请描述您的问题',
					icon: 'none',
					duration: 2000
				});
				return false;
			}
			uni.request({
				url: this.url + 'advicefeedback/',
				method: 'POST',
				data: {
					title: this.title,
					message: this.desc
				},
				header: {
					Authorization: 'JWT' + ' ' + this.global_.token
				},
				success(res) {
					console.log(res);
					if (res.statusCode == 200) {
						_this.hidden = true;
						_this.title = '';
						_this.desc = '';
						uni.showToast({
							title: '提交成功',
							icon: 'none',
							duration: 2000
						});
					} else {
						uni.showToast({
							title: '请输入文字信息',
							icon: 'none',
							duration: 2000
						});
					}

					var page = getCurrentPages().pop();
					if (page == undefined || page == null) return;
					page.onLoad();
				}
			});
		},
		detail: function(item) {
			var mes = JSON.stringify(item);
			uni.navigateTo({
				url: '../suggest-detail/suggest-detail?message=' + mes
			});
		},
		identity: function() {
			uni.navigateTo({
				url: '../identity/identity'
			});
		}
	}
};
